// auth-clerk/src/hooks/useAuth.js
import { useUser, useAuth as useClerkAuth, useClerk } from '@clerk/clerk-react';
import { useState, useEffect, useCallback } from 'react';

//  Function URL
const LAMBDA_API_URL = 'https://ykyc7xcyfmacka6oqeqgfhrtt40xvynm.lambda-url.ap-northeast-1.on.aws/';

//  API URL
const VIDEO_API_URL = 'https://len2k4bksqc6jqwapucgpczecu0jugyb.lambda-url.ap-northeast-1.on.aws/';

export const useAuth = () => {
  const { user, isLoaded: userLoaded } = useUser();
  const { isSignedIn, isLoaded: authLoaded, getToken } = useClerkAuth(); //  getToken
  const clerk = useClerk();
  
  // 
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(false);

  //  - 
  const getAdminEmails = () => {
    // 
    const envAdmins = process.env.REACT_APP_ADMIN_EMAILS;
    if (envAdmins) {
      return envAdmins.split(',').map(email => email.trim());
    }
    
    // 
    return ['ops@damonxuda.site'];
  };

  // 
  const isAdmin = () => {
    if (!user) return false;
    
    const adminEmails = getAdminEmails();
    const userEmail = user.emailAddresses[0]?.emailAddress;
    
    return adminEmails.includes(userEmail);
  };

  // 
  const isOwner = () => {
    if (!user) return false;
    
    const userEmail = user.emailAddresses[0]?.emailAddress;
    return userEmail === 'ops@damonxuda.site'; // 
  };

  // 
  const hasModuleAccess = (moduleName) => {
    //  false
    if (!userLoaded || !isSignedIn || !user) return false;
    
    // 
    if (isAdmin()) return true;
    
    //  publicMetadata 
    const authorizedModules = user.publicMetadata?.authorized_modules || [];
    return authorizedModules.includes(moduleName);
  };

  // 
  const getUserModules = () => {
    if (!user) return [];
    
    // 
    if (isAdmin()) return ['quiz', 'future1', 'future2'];
    
    return user.publicMetadata?.authorized_modules || [];
  };

  // 
  const getUserPermissionInfo = () => {
    if (!user) return null;
    
    return {
      modules: getUserModules(),
      approvedBy: user.publicMetadata?.approved_by || null,
      approvedAt: user.publicMetadata?.approved_at || null,
      lastUpdated: user.publicMetadata?.updated_at || null
    };
  };

  // - 
  const fetchAllUsers = useCallback(async () => {
    if (!isAdmin()) {
      return;
    }
    
    setLoading(true);
    try {
      const response = await fetch(LAMBDA_API_URL);
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const data = await response.json();
      setUsers(data.users || []);

    } catch (error) {
      console.error(':', error);
      setUsers([]);
    } finally {
      setLoading(false);
    }
  }, [isAdmin]);

  //  Lambda API- 
  const assignModuleAccess = async (userEmailOrId, moduleOrModules, isGranting = true) => {
    if (!isAdmin()) {
      throw new Error('');
    }

    try {
      // 
      if (typeof moduleOrModules === 'string' && typeof isGranting === 'boolean') {
        console.log(` :`);
        console.log(`   - : ${userEmailOrId}`);
        console.log(`   - : ${moduleOrModules}`);
        console.log(`   - : ${isGranting ? '' : ''}`);
        
        // 1. 
        console.log(` ...`);
        const allUsers = await fetchAllUsers();
        console.log(`  ${allUsers.length} `);
        
        // 2.  - 
        console.log(` : ${userEmailOrId}`);
        const targetUser = allUsers.find(u => {
          const userEmail = u.email || u.emailAddresses?.[0]?.emailAddress || 'no-email';
          const match = u.email === userEmailOrId || 
                      u.id === userEmailOrId ||
                      userEmail === userEmailOrId;
          
          if (match) {
            console.log(` :`, {
              id: u.id,
              email: userEmail,
              currentModules: u.modules
            });
          }
          
          return match;
        });
        
        if (!targetUser) {
          console.error(` !`);
          console.log(`:`, allUsers.map(u => ({
            id: u.id,
            email: u.email || u.emailAddresses?.[0]?.emailAddress,
            modules: u.modules
          })));
          throw new Error(`: ${userEmailOrId}`);
        }
        
        // 3. 
        const currentModules = targetUser.modules || [];
        console.log(` :`, currentModules);
        console.log(` :`, typeof currentModules, Array.isArray(currentModules));
        
        // 4. 
        let newModules;
        if (isGranting) {
          // 
          if (currentModules.includes(moduleOrModules)) {
            console.log(`   ${moduleOrModules} `);
            newModules = [...currentModules];
          } else {
            newModules = [...currentModules, moduleOrModules];
            console.log(`  ${moduleOrModules}`);
          }
        } else {
          // 
          newModules = currentModules.filter(m => m !== moduleOrModules);
          console.log(`  ${moduleOrModules}`);
        }
        
        console.log(` :`);
        console.log(`   - : [${currentModules.join(', ')}]`);
        console.log(`   - : [${newModules.join(', ')}]`);
        
        // 5. 
        const hasChanges = JSON.stringify(currentModules.sort()) !== JSON.stringify(newModules.sort());
        console.log(` : ${hasChanges}`);
        
        if (!hasChanges) {
          console.log(`  API`);
          return { success: true, message: '' };
        }
        
        // 6. API
        const requestBody = {
          action: 'assign_modules',
          userId: targetUser.id,
          modules: newModules,
          approvedBy: user.emailAddresses[0].emailAddress
        };
        
        console.log(` API:`, requestBody);
        
        const response = await fetch(LAMBDA_API_URL, {
          method: 'POST',
          mode: 'cors',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(requestBody)
        });

        console.log(` API: ${response.status}`);
        
        if (!response.ok) {
          const errorText = await response.text();
          console.error(` API:`, errorText);
          throw new Error(`HTTP error! status: ${response.status}, message: ${errorText}`);
        }

        const result = await response.json();
        console.log(` API:`, result);
        
        if (!result.success) {
          console.error(` API:`, result);
          throw new Error(result.error || '');
        }

        console.log(` `);
        
        // 7. 
        console.log(` ...`);
        await new Promise(resolve => setTimeout(resolve, 1000)); // 1
        
        try {
          await fetchAllUsers(); // 
          console.log(` `);
        } catch (reloadError) {
          console.warn(`  :`, reloadError);
        }

        return { 
          success: true, 
          message: ` ${moduleOrModules} ${isGranting ? '' : ''}`,
          details: {
            userId: targetUser.id,
            email: userEmailOrId,
            oldModules: currentModules,
            newModules: newModules
          }
        };
        
      } else {
        // 
        const modules = Array.isArray(moduleOrModules) ? moduleOrModules : [moduleOrModules];
        
        const requestBody = {
          action: 'assign_modules',
          userId: userEmailOrId,
          modules: modules,
          approvedBy: user.emailAddresses[0].emailAddress
        };
        
        const response = await fetch(LAMBDA_API_URL, {
          method: 'POST',
          mode: 'cors',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(requestBody)
        });

        if (!response.ok) {
          const errorText = await response.text();
          throw new Error(`HTTP error! status: ${response.status}, message: ${errorText}`);
        }

        const result = await response.json();
        
        if (!result.success) {
          throw new Error(result.error || '');
        }

        return { success: true };
      }
      
    } catch (error) {
      console.error(' :', error);
      console.error(' :', error.stack);
      throw error;
    }
  };

  //  Lambda API- 
  const revokeModuleAccess = async (userId) => {
    if (!isAdmin()) {
      throw new Error('');
    }

    try {
      const response = await fetch(LAMBDA_API_URL, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          action: 'revoke_modules',
          userId: userId,
          revokedBy: user.emailAddresses[0].emailAddress
        })
      });

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const result = await response.json();
      if (!result.success) {
        throw new Error(result.error || '');
      }

      return { success: true };
    } catch (error) {
      console.error(':', error);
      throw error;
    }
  };

  // UserManagement- 
  const getUserPermissions = (targetUser) => {
    if (!targetUser) return { modules: [], approvedBy: null, approvedAt: null };
    
    return {
      modules: targetUser.modules || targetUser.publicMetadata?.authorized_modules || [],
      approvedBy: targetUser.approved_by || targetUser.publicMetadata?.approved_by || null,
      approvedAt: targetUser.approved_at || targetUser.publicMetadata?.approved_at || null
    };
  };

  // =====  API =====
  
  // token
  const fetchVideoList = async (path = '') => {
    try {
      console.log(' , path:', path);
      
      // Clerk token
      const token = await getToken();
      console.log(' token:', token ? '' : '');
      
      const response = await fetch(
        `${VIDEO_API_URL}/videos/list?path=${encodeURIComponent(path)}`,
        {
          headers: { 
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${token}`
          }
        }
      );
      
      console.log(' API:', response.status);
      
      if (!response.ok) {
        const errorText = await response.text();
        throw new Error(`HTTP error! status: ${response.status}, message: ${errorText}`);
      }
      
      const data = await response.json();
      console.log(' :', data.length, '');
      return data;
      
    } catch (error) {
      console.error(' :', error);
      throw error;
    }
  };

  // URLtoken
  const getVideoUrl = async (videoKey) => {
    try {
      console.log(' URL, key:', videoKey);
      
      // Clerk token
      const token = await getToken();
      
      const response = await fetch(
        `${VIDEO_API_URL}/videos/url/${encodeURIComponent(videoKey)}`,
        {
          headers: { 
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${token}`
          }
        }
      );
      
      if (!response.ok) {
        const errorText = await response.text();
        throw new Error(`HTTP error! status: ${response.status}, message: ${errorText}`);
      }
      
      const data = await response.json();
      console.log(' URL');
      return data;
      
    } catch (error) {
      console.error(' URL:', error);
      throw error;
    }
  };

  // - 
  useEffect(() => {
    if (userLoaded && isSignedIn && isAdmin()) {
      fetchAllUsers();
    }
  }, [userLoaded, isSignedIn, user]);

  return {
    //  - 
    user,
    isSignedIn: !!isSignedIn,
    isLoaded: userLoaded && authLoaded,
    isAdmin: isAdmin(),
    isOwner: isOwner(),
    authLoading: !userLoaded || !authLoaded,
    // 
    getAdminEmails,
    // 
    hasModuleAccess,
    getUserModules,
    getUserPermissionInfo,
    //  - 
    users,
    loading: loading,
    fetchAllUsers,
    assignModuleAccess,
    revokeModuleAccess,
    getUserPermissions,
    
    //  
    fetchVideoList,
    getVideoUrl,
    getToken
  };
};