// QuestionInput/index.js - LaTeX
import React, { useState, useEffect } from "react";
import { Plus, Upload } from "lucide-react";
import ZipUploadComponent from "./ZipUploadComponent";
import BatchImportForm from "./BatchImportForm";
import { MarkdownParser } from "../../services/MarkdownParser"; // 

// 
const QUESTION_TYPES = ["", ""];

const QuestionInput = ({ questions, setQuestions, db, user }) => {
  const [showAddForm, setShowAddForm] = useState(false);
  const [showBatchImport, setShowBatchImport] = useState(false);
  const [papers, setPapers] = useState([]);

  // 
  const [questionForm, setQuestionForm] = useState({
    paperId: "",
    question_type: "",
    question_number: "",
    question_text: "",
    answer: "",
  });

  // 
  const [batchForm, setBatchForm] = useState({
    teacher: "",
    semester: "",
    course_name: "",
    math_category: "",
    markdownText: "",
  });

  // 
  const [imageMap, setImageMap] = useState({});

  // 
  const generatePaperTitle = (
    teacher,
    semester,
    course_name,
    math_category
  ) => {
    const parts = [teacher, semester, course_name].filter(Boolean);
    if (parts.length === 0) {
      return `${math_category}`;
    }
    return parts.join("");
  };

  // 
  useEffect(() => {
    const loadPapers = async () => {
      try {
        const result = await db.getPapers();
        if (result.success) {
          setPapers(result.data || []);
        }
      } catch (error) {
        console.error(":", error);
      }
    };

    if (user) {
      loadPapers();
    }
  }, [db, user]);

  // 
  const handleSingleQuestionSubmit = async (e) => {
    e.preventDefault();

    if (!questionForm.paperId) {
      alert("");
      return;
    }

    try {
      const result = await db.addQuestion({
        paperId: questionForm.paperId,
        question_type: questionForm.question_type,
        question_number: questionForm.question_number,
        question_text: questionForm.question_text,
        answer: questionForm.answer,
      });

      if (!result.success) throw new Error(result.error);

      // 
      const questionsResult = await db.getQuestions();
      if (questionsResult.success) {
        setQuestions(questionsResult.data || []);
      }

      setQuestionForm({
        paperId: "",
        question_type: "",
        question_number: "",
        question_text: "",
        answer: "",
      });
      setShowAddForm(false);
      alert("");
    } catch (error) {
      console.error(":", error);
      alert("" + error.message);
    }
  };

  // +LaTeX
  const handleBatchImport = async (e, imageMap = {}) => {
    e.preventDefault();

    if (
      !batchForm.teacher ||
      !batchForm.semester ||
      !batchForm.course_name ||
      !batchForm.math_category ||
      !batchForm.markdownText
    ) {
      alert("");
      return;
    }

    try {
      // 
      const title = generatePaperTitle(
        batchForm.teacher,
        batchForm.semester,
        batchForm.course_name,
        batchForm.math_category
      );

      // MarkdownParser
      const parser = new MarkdownParser();
      const parseResult = await parser.parseMarkdownQuestions(
        batchForm.markdownText,
        imageMap
      );

      if (!parseResult.success) throw new Error(parseResult.error);

      const questions = parseResult.data;
      if (questions.length === 0) {
        throw new Error("");
      }

      console.log("LaTeX:", questions);

      // 
      const result = await db.addPaperWithQuestions(
        {
          title: title,
          teacher: batchForm.teacher,
          semester: batchForm.semester,
          course_name: batchForm.course_name,
          math_category: batchForm.math_category,
        },
        questions,
        imageMap
      );

      if (!result.success) throw new Error(result.error);

      // 
      const papersResult = await db.getPapers();
      if (papersResult.success) {
        setPapers(papersResult.data || []);
      }

      const questionsResult = await db.getQuestions();
      if (questionsResult.success) {
        setQuestions(questionsResult.data || []);
      }

      setBatchForm({
        teacher: "",
        semester: "",
        course_name: "",
        math_category: "",
        markdownText: "",
      });
      setImageMap({}); // 
      setShowBatchImport(false);

      // 
      const importedQuestions = result.data.questions || [];
      const questionCount = importedQuestions.length;

      // LaTeX
      const hasLatexContent = questions.some(
        (q) =>
          parser.hasLatexContent(q.question_text) ||
          parser.hasLatexContent(q.answer)
      );

      const message = hasLatexContent
        ? `"${title}" ${questionCount} `
        : `"${title}" ${questionCount} `;

      alert(message);
    } catch (error) {
      console.error(":", error);
      alert("" + error.message);
    }
  };

  // 
  const handleImagesUploaded = (uploadedImageMap) => {
    setImageMap(uploadedImageMap);
    console.log(":", uploadedImageMap);
  };

  // 
  const handleBatchImportCancel = () => {
    setShowBatchImport(false);
    setBatchForm({
      teacher: "",
      semester: "",
      course_name: "",
      math_category: "",
      markdownText: "",
    });
    // 
  };

  // 
  const canAddQuestions = () => {
    return user && user.emailAddresses && user.emailAddresses.length > 0;
  };

  return (
    <div>
      {/*  */}
      {!canAddQuestions() && (
        <div className="mb-4 p-4 bg-amber-50 border border-amber-200 rounded-lg">
          <p className="text-sm text-amber-800">
            <strong></strong>
          </p>
        </div>
      )}

      {/*  */}
      <div className="flex gap-2 mb-6">
        <button
          onClick={() => setShowBatchImport(!showBatchImport)}
          disabled={!canAddQuestions()}
          className={`px-4 py-2 rounded-lg flex items-center gap-2 transition-colors ${
            canAddQuestions()
              ? "bg-green-500 hover:bg-green-600 text-white"
              : "bg-gray-300 text-gray-500 cursor-not-allowed"
          }`}
        >
          <Upload size={20} />
          
        </button>
        <button
          onClick={() => setShowAddForm(!showAddForm)}
          disabled={!canAddQuestions()}
          className={`px-4 py-2 rounded-lg flex items-center gap-2 transition-colors ${
            canAddQuestions()
              ? "bg-blue-500 hover:bg-blue-600 text-white"
              : "bg-gray-300 text-gray-500 cursor-not-allowed"
          }`}
        >
          <Plus size={20} />
          
        </button>
      </div>

      {/*  */}
      {showBatchImport && canAddQuestions() && (
        <div className="mb-6 p-6 bg-gray-50 border border-gray-200 rounded-lg">
          <h3 className="text-lg font-semibold mb-4"></h3>

          {/* Zip */}
          <ZipUploadComponent
            onImagesUploaded={handleImagesUploaded}
            imageMap={imageMap}
            db={db}
          />

          {/*  */}
          <BatchImportForm
            batchForm={batchForm}
            setBatchForm={setBatchForm}
            imageMap={imageMap}
            onSubmit={handleBatchImport}
            onCancel={handleBatchImportCancel}
          />
        </div>
      )}

      {/*  */}
      {showAddForm && canAddQuestions() && (
        <div className="mb-6 p-6 bg-gray-50 border border-gray-200 rounded-lg">
          <h3 className="text-lg font-semibold mb-4"></h3>
          <form onSubmit={handleSingleQuestionSubmit}>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
              <div className="md:col-span-2">
                <label className="block text-sm font-medium text-gray-700 mb-1">
                   *
                </label>
                <select
                  value={questionForm.paperId}
                  onChange={(e) =>
                    setQuestionForm({
                      ...questionForm,
                      paperId: e.target.value,
                    })
                  }
                  className="w-full p-2 border border-gray-300 rounded-lg"
                  required
                >
                  <option value=""></option>
                  {papers.map((paper) => (
                    <option key={paper.id} value={paper.id}>
                      {paper.title} - {paper.teacher} ({paper.math_category})
                    </option>
                  ))}
                </select>
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                   *
                </label>
                <select
                  value={questionForm.question_type}
                  onChange={(e) =>
                    setQuestionForm({
                      ...questionForm,
                      question_type: e.target.value,
                    })
                  }
                  className="w-full p-2 border border-gray-300 rounded-lg"
                >
                  {QUESTION_TYPES.map((type) => (
                    <option key={type} value={type}>
                      {type}
                    </option>
                  ))}
                </select>
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  
                </label>
                <input
                  type="text"
                  value={questionForm.question_number}
                  onChange={(e) =>
                    setQuestionForm({
                      ...questionForm,
                      question_number: e.target.value,
                    })
                  }
                  placeholder="11"
                  className="w-full p-2 border border-gray-300 rounded-lg"
                />
              </div>
              <div className="md:col-span-2">
                <label className="block text-sm font-medium text-gray-700 mb-1">
                   *{" "}
                  <span className="text-blue-600 text-xs">
                    LaTeX
                  </span>
                </label>
                <textarea
                  value={questionForm.question_text}
                  onChange={(e) =>
                    setQuestionForm({
                      ...questionForm,
                      question_text: e.target.value,
                    })
                  }
                  placeholder="LaTeX"
                  className="w-full p-3 border border-gray-300 rounded-lg"
                  rows={4}
                  required
                />
              </div>
              <div className="md:col-span-2">
                <label className="block text-sm font-medium text-gray-700 mb-1">
                   *{" "}
                  <span className="text-blue-600 text-xs">
                    LaTeX
                  </span>
                </label>
                <textarea
                  value={questionForm.answer}
                  onChange={(e) =>
                    setQuestionForm({ ...questionForm, answer: e.target.value })
                  }
                  placeholder="LaTeX"
                  className="w-full p-3 border border-gray-300 rounded-lg"
                  rows={3}
                  required
                />
              </div>
            </div>

            <div className="flex gap-2">
              <button
                type="submit"
                className="px-4 py-2 bg-blue-500 hover:bg-blue-600 text-white rounded-lg"
              >
                
              </button>
              <button
                type="button"
                onClick={() => setShowAddForm(false)}
                className="px-4 py-2 bg-gray-500 hover:bg-gray-600 text-white rounded-lg"
              >
                
              </button>
            </div>
          </form>
        </div>
      )}

      {/*  */}
      {papers.length > 0 && (
        <div className="bg-white p-4 rounded-lg border">
          <h4 className="font-medium text-gray-800 mb-3"></h4>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-3">
            {papers.map((paper) => (
              <div key={paper.id} className="p-3 bg-gray-50 rounded border">
                <div className="font-medium text-sm">{paper.title}</div>
                <div className="text-xs text-gray-600 mt-1">
                  {paper.teacher}  {paper.math_category}  {paper.semester}
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      {/*  */}
      {user && (
        <div className="mt-4 p-3 bg-green-50 border border-green-200 rounded-lg">
          <p className="text-sm text-green-800">
            <strong></strong>
            {user.emailAddresses?.[0]?.emailAddress}
          </p>
          <p className="text-xs text-green-600 mt-1">
            LaTeX
          </p>
        </div>
      )}
    </div>
  );
};

export default QuestionInput;
