// ZipUploadComponent.js - ZIP
import React, { useState } from "react";
import ImageManager from "./ImageManager";

const ZipUploadComponent = ({ onImagesUploaded, imageMap, db }) => {
  const [uploading, setUploading] = useState(false);
  const [uploadResults, setUploadResults] = useState(null);

  const handleZipUpload = async (event) => {
    const file = event.target.files[0];
    if (!file) return;

    // 
    if (!file.name.toLowerCase().endsWith(".zip")) {
      alert("zip");
      return;
    }

    // 50MB
    if (file.size > 50 * 1024 * 1024) {
      alert("zip50MB");
      return;
    }

    setUploading(true);
    setUploadResults(null);

    try {
      console.log("zip...");
      const result = await db.uploadImagesFromZip(file);

      if (result.success) {
        setUploadResults(result);

        // 
        const newImageMap = { ...imageMap };
        result.uploads.forEach((upload) => {
          newImageMap[upload.originalName] = {
            url: upload.url,
            storageName: upload.storageName,
          };
        });

        onImagesUploaded(newImageMap);

        alert(
          ` ${result.uploadedCount} ${result.errors.length > 0 ? `${result.errors.length} ` : ""}`
        );
      } else {
        alert("" + result.error);
      }
    } catch (error) {
      console.error(":", error);
      alert("" + error.message);
    } finally {
      setUploading(false);
    }
  };

  // 
  const handleDeleteImage = async (originalName) => {
    if (!window.confirm(` ${originalName} `)) {
      return;
    }

    const imageInfo = imageMap[originalName];
    if (!imageInfo) return;

    try {
      const result = await db.deleteImage(imageInfo.storageName);
      if (result.success) {
        const newImageMap = { ...imageMap };
        delete newImageMap[originalName];
        onImagesUploaded(newImageMap);
        alert("");
      } else {
        alert("" + result.error);
      }
    } catch (error) {
      console.error(":", error);
      alert("" + error.message);
    }
  };

  // 
  const handleReplaceImage = async (originalName, storageName, newFile) => {
    try {
      const result = await db.replaceImage(storageName, newFile);
      if (result.success) {
        const newImageMap = { ...imageMap };
        newImageMap[originalName] = {
          url: result.url,
          storageName: result.storageName,
        };
        onImagesUploaded(newImageMap);
        alert("");
      } else {
        alert("" + result.error);
      }
    } catch (error) {
      console.error(":", error);
      alert("" + error.message);
    }
  };

  // 
  const handleClearAll = async () => {
    if (!window.confirm("")) {
      return;
    }

    const storageNames = Object.values(imageMap).map((img) => img.storageName);
    if (storageNames.length === 0) return;

    try {
      const result = await db.deleteImages(storageNames);
      if (result.success) {
        onImagesUploaded({});
        setUploadResults(null);
        alert("");
      } else {
        alert("" + result.error);
      }
    } catch (error) {
      console.error(":", error);
      alert("" + error.message);
    }
  };

  return (
    <div className="mb-6 p-4 bg-green-50 border border-green-200 rounded-lg">
      <h4 className="font-medium text-green-800 mb-3"></h4>

      <div className="mb-4">
        <input
          type="file"
          accept=".zip"
          onChange={handleZipUpload}
          disabled={uploading}
          className="mb-2"
        />
        {uploading && (
          <div className="flex items-center gap-2 text-blue-600 text-sm">
            <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-blue-600"></div>
            zip...
          </div>
        )}
        <p className="text-xs text-green-600">
          zip01.png, 02.png, 03.png...
        </p>
      </div>

      {/*  */}
      {uploadResults && (
        <div className="mt-4 p-3 bg-white rounded border">
          <h5 className="font-medium text-green-700 mb-2"></h5>
          <div className="text-sm text-gray-700 space-y-1">
            <p>
              :{" "}
              <span className="font-medium text-green-600">
                {uploadResults.uploadedCount}
              </span>{" "}
              
            </p>
            {uploadResults.errors.length > 0 && (
              <p>
                :{" "}
                <span className="font-medium text-red-600">
                  {uploadResults.errors.length}
                </span>{" "}
                
              </p>
            )}
          </div>

          {/*  */}
          {uploadResults.errors.length > 0 && (
            <div className="mt-3">
              <p className="text-sm font-medium text-red-700 mb-2">
                :
              </p>
              <div className="space-y-1">
                {uploadResults.errors.map((error, index) => (
                  <div key={index} className="text-xs text-red-600">
                    {error.filename}: {error.error}
                  </div>
                ))}
              </div>
            </div>
          )}
        </div>
      )}

      {/*  */}
      <ImageManager
        imageMap={imageMap}
        onImageDelete={handleDeleteImage}
        onImageReplace={handleReplaceImage}
        onClearAll={handleClearAll}
      />
    </div>
  );
};

export default ZipUploadComponent;
