import { useAuth, UserProfile } from '../../../auth-clerk/src';
import React, { useState, useEffect } from 'react';
import { User } from 'lucide-react';
import db from '../services/DatabaseService.js';
import AdminSection from './AdminSection';
import BrowseSection from './BrowseSection';
import PracticeSection from './PracticeSection';

const QuizMain = () => {
  const [activeTab, setActiveTab] = useState('input');
  const [questions, setQuestions] = useState([]);
  const [papers, setPapers] = useState([]);
  const [attempts, setAttempts] = useState([]);
  const [loading, setLoading] = useState(true);
  
  //  - Clerk
  const { user, isSignedIn, isAdmin, loading: authLoading } = useAuth();
  
  // 
  const [filters, setFilters] = useState({
    teacher: '',
    semester: '',
    category: '',
    paperId: '',
    masteryLevel: '',
    courseName: ''
  });

  // 
  const mathCategories = ['', '', '', '', '', '', ''];

  // 
  useEffect(() => {
    const initializeSystem = async () => {
      if (!isSignedIn || authLoading || !user) return;
      
      try {
        // 
        await db.initializeSupabase();

        // 
        await db.debugDatabaseState();
        
        // 
        const [questionsResult, papersResult, attemptsResult] = await Promise.all([
          db.getQuestions(),
          db.getPapers(),
          db.getAttempts({ userId: user.id })
        ]);
        
        if (!questionsResult.success || !papersResult.success || !attemptsResult.success) {
          throw new Error('');
        }
        
        setQuestions(questionsResult.data || []);
        setPapers(papersResult.data || []);
        setAttempts(attemptsResult.data || []);
      } catch (error) {
        console.error(':', error);
        alert('');
      } finally {
        setLoading(false);
        
        // 
        if (!isAdmin && (activeTab === 'input' || activeTab === 'deploy')) {
          setActiveTab('browse');
        }
      }
    };
    
    initializeSystem();
  }, [isSignedIn, authLoading, user, isAdmin, activeTab]);

  // 
  const addAttempt = async (questionId, score) => {
    const attempt = {
      questionId,
      userId: user.id,
      masteryScore: score,
      isMarkedWrong: false
    };
    
    try {
      const result = await db.recordAttempt(attempt);
      if (!result.success) throw new Error(result.error);
      
      setAttempts([...attempts, result.data]);
      alert(` ${score} `);
    } catch (error) {
      console.error(':', error);
      alert('');
    }
  };

  // 
  const toggleWrongQuestion = async (questionId) => {
    const currentlyWrong = isMarkedWrong(questionId);
    try {
      const attempt = {
        questionId,
        userId: user.id,
        masteryScore: null, // 
        isMarkedWrong: !currentlyWrong
      };
      
      const result = await db.recordAttempt(attempt);
      if (!result.success) throw new Error(result.error);
      
      setAttempts([...attempts, result.data]);
      alert(`${currentlyWrong ? '' : ''}`);
    } catch (error) {
      console.error(':', error);
      alert('');
    }
  };

  // 
  const getCurrentScore = (questionId) => {
    const userAttempts = attempts
      .filter(a => a.question_id === questionId && a.user_id === user.id && a.mastery_score > 0)
      .sort((a, b) => new Date(b.created_at) - new Date(a.created_at));
    
    return userAttempts.length > 0 ? userAttempts[0].mastery_score : 0;
  };

  // 
  const isMarkedWrong = (questionId) => {
    const userAttempts = attempts
      .filter(a => a.question_id === questionId && a.user_id === user.id)
      .sort((a, b) => new Date(b.created_at) - new Date(a.created_at));
    
    return userAttempts.length > 0 ? userAttempts[0].is_marked_wrong : false;
  };

  // 
  const getTeachers = () => {
    return [...new Set(papers.map(p => p.teacher))].filter(Boolean);
  };

  // 
  const getSemesters = () => {
    return [...new Set(papers.map(p => p.semester))].filter(Boolean);
  };

  if (authLoading || loading) {
    return (
      <div className="max-w-6xl mx-auto p-6 bg-gray-50 min-h-screen flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-blue-500 mx-auto"></div>
          <p className="mt-4 text-gray-600">...</p>
        </div>
      </div>
    );
  }

  // props
  const sharedProps = {
    questions,
    setQuestions,
    papers,
    attempts,
    setAttempts,
    filters,
    setFilters,
    mathCategories,
    addAttempt,
    toggleWrongQuestion,
    getCurrentScore,
    isMarkedWrong,
    getTeachers,
    getSemesters,
    user,
    isAdmin,
    db
  };

  return (
    <div className="max-w-6xl mx-auto p-6 bg-gray-50 min-h-screen">
      <div className="bg-white rounded-lg shadow-lg">
        {/*  */}
        <div className="px-6 py-4 bg-gray-50 border-b border-gray-200 rounded-t-lg">
          <div className="flex justify-between items-center">
            <div className="flex items-center space-x-4">
              <h1 className="text-xl font-bold text-gray-900"></h1>
              <div className="flex items-center space-x-2 text-sm text-gray-600">
                <User size={16} />
                <span>{user?.emailAddresses?.[0]?.emailAddress || user?.firstName}</span>
                {isAdmin && (
                  <span className="px-2 py-1 bg-blue-100 text-blue-800 rounded text-xs"></span>
                )}
              </div>
            </div>
            {/*  */}
            <UserProfile showWelcome={false} afterSignOutUrl="/" />
          </div>
          <div className="mt-2">
            <p className="text-sm text-gray-600">
              
            </p>
          </div>
        </div>

        {/*  */}
        <div className="border-b border-gray-200">
          <nav className="flex space-x-8 px-6">
            {/*  -  */}
            {isAdmin && (
              <button
                onClick={() => setActiveTab('input')}
                className={`py-4 px-2 border-b-2 font-medium text-sm ${
                  activeTab === 'input' 
                    ? 'border-blue-500 text-blue-600' 
                    : 'border-transparent text-gray-500 hover:text-gray-700'
                }`}
              >
                
              </button>
            )}
            
            <button
              onClick={() => setActiveTab('browse')}
              className={`py-4 px-2 border-b-2 font-medium text-sm ${
                activeTab === 'browse' 
                  ? 'border-blue-500 text-blue-600' 
                  : 'border-transparent text-gray-500 hover:text-gray-700'
              }`}
            >
               ({questions.length})
            </button>
            
            <button
              onClick={() => setActiveTab('practice')}
              className={`py-4 px-2 border-b-2 font-medium text-sm ${
                activeTab === 'practice' 
                  ? 'border-blue-500 text-blue-600' 
                  : 'border-transparent text-gray-500 hover:text-gray-700'
              }`}
            >
              
            </button>
            
            {/*  -  */}
            {isAdmin && (
              <button
                onClick={() => setActiveTab('deploy')}
                className={`py-4 px-2 border-b-2 font-medium text-sm ${
                  activeTab === 'deploy' 
                    ? 'border-blue-500 text-blue-600' 
                    : 'border-transparent text-gray-500 hover:text-gray-700'
                }`}
              >
                 
              </button>
            )}
          </nav>
        </div>

        {/*  */}
        <div className="p-6">
          {/*  */}
          {isAdmin && (activeTab === 'input' || activeTab === 'deploy') && (
            <AdminSection 
              activeTab={activeTab}
              {...sharedProps}
            />
          )}
          
          {/*  */}
          {activeTab === 'browse' && (
            <BrowseSection {...sharedProps} />
          )}
          
          {/*  */}
          {activeTab === 'practice' && (
            <PracticeSection {...sharedProps} />
          )}
        </div>
      </div>
    </div>
  );
};

export default QuizMain;