// ImageService.js - 
import JSZip from "jszip";

export class ImageService {
  constructor(supabase) {
    this.supabase = supabase;
  }

  // 
  async deleteImage(storageName) {
    try {
      if (!this.supabase) {
        throw new Error("");
      }

      const { error } = await this.supabase.storage
        .from("question-images")
        .remove([storageName]);

      if (error) throw error;

      return { success: true };
    } catch (error) {
      console.error(":", error);
      return { success: false, error: error.message };
    }
  }

  // 
  async deleteImages(storageNames) {
    try {
      if (!this.supabase) {
        throw new Error("");
      }

      const { error } = await this.supabase.storage
        .from("question-images")
        .remove(storageNames);

      if (error) throw error;

      return { success: true };
    } catch (error) {
      console.error(":", error);
      return { success: false, error: error.message };
    }
  }

  // 
  async replaceImage(oldStorageName, newImageFile) {
    try {
      if (!this.supabase) {
        throw new Error("");
      }

      // 
      await this.deleteImage(oldStorageName);

      // 
      const { data, error } = await this.supabase.storage
        .from("question-images")
        .upload(oldStorageName, newImageFile);

      if (error) throw error;

      // URL
      const { data: urlData } = this.supabase.storage
        .from("question-images")
        .getPublicUrl(oldStorageName);

      return {
        success: true,
        url: urlData.publicUrl,
        storageName: oldStorageName,
      };
    } catch (error) {
      console.error(":", error);
      return { success: false, error: error.message };
    }
  }

  // Supabase Storage
  async uploadImagesFromZip(zipFile, paperUUID = null) {
    try {
      if (!this.supabase) {
        throw new Error("");
      }

      console.log("zip...");

      // zip
      const zip = new JSZip();
      const zipData = await zip.loadAsync(zipFile);

      const uploadResults = [];
      const errors = [];

      // zip
      for (const filename in zipData.files) {
        const file = zipData.files[filename];

        // 
        if (
          file.dir ||
          !this.isImageFile(filename) ||
          filename.startsWith("__MACOSX") ||
          filename.startsWith("._") ||
          filename.includes("/.")
        ) {
          console.log(`: ${filename}`);
          continue;
        }

        try {
          console.log(`: ${filename}`);

          // zip
          const imageBlob = await file.async("blob");

          // 
          let storageFileName;
          if (paperUUID) {
            // UUIDUUID_
            storageFileName = `${paperUUID}_${filename}`;
          } else {
            // 
            storageFileName = `temp_${Date.now()}_${filename}`;
          }

          // Supabase Storage
          const { data, error } = await this.supabase.storage
            .from("question-images")
            .upload(storageFileName, imageBlob);

          if (error) {
            throw error;
          }

          // URL
          const { data: urlData } = this.supabase.storage
            .from("question-images")
            .getPublicUrl(storageFileName);

          uploadResults.push({
            originalName: filename,
            storageName: storageFileName,
            url: urlData.publicUrl,
          });

          console.log(` : ${filename}`);
        } catch (error) {
          console.error(`  ${filename}:`, error);
          errors.push({ filename, error: error.message });
        }
      }

      return {
        success: true,
        uploadedCount: uploadResults.length,
        uploads: uploadResults,
        errors: errors,
      };
    } catch (error) {
      console.error("zip:", error);
      return {
        success: false,
        error: error.message,
      };
    }
  }

  // 
  async renameTemporaryImages(imageMap, paperUUID) {
    try {
      for (const [originalName, imageInfo] of Object.entries(imageMap)) {
        const oldName = imageInfo.storageName;
        const newName = `${paperUUID}_${originalName}`;

        if (oldName.startsWith("temp_")) {
          // /
          const { data, error } = await this.supabase.storage
            .from("question-images")
            .move(oldName, newName);

          if (error) {
            console.error(` ${oldName} -> ${newName}:`, error);
          } else {
            // imageMap
            imageInfo.storageName = newName;
            const { data: urlData } = this.supabase.storage
              .from("question-images")
              .getPublicUrl(newName);
            imageInfo.url = urlData.publicUrl;
            console.log(` : ${oldName} -> ${newName}`);
          }
        }
      }
    } catch (error) {
      console.error(":", error);
    }
  }

  // 
  isImageFile(filename) {
    const imageExtensions = [".png", ".jpg", ".jpeg", ".gif", ".webp"];
    const ext = filename.toLowerCase().substring(filename.lastIndexOf("."));
    return imageExtensions.includes(ext);
  }
}
